import React, { Component, useState } from 'react';
import { Table, Input, InputNumber, Button, Form, Modal, message, Row, Col, Menu, Select } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/loans.service";
import { Typography } from "antd";
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";

const { Title } = Typography;
const { confirm } = Modal;
const { Option } = Select;

const EditableTable = ({systemData}) => {
  const [form] = Form.useForm();
  const [editingKey, setEditingKey] = useState('');
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);

  const { loans, loansLoading, allLoans, allLoansLoading } = systemData;
  let history = useHistory();

  const viewDetails = row => {
    
    setEditingKey(row._id);
    setVisible(true);
    form.setFieldsValue({
      bankName:row.bankName,
      purpose:row.purpose,
      amount:row.amount,
      months:row.months,
      interest:row.interest,
      penalty:row.penalty,
    });
  }

  const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);

  const confirmDelete = (row) => {
    confirm({
      title: 'Do you Want to delete this loan?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        remove(row._id);
      }
    });
  };
	

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async e => {
    try {
      const values = await form.validateFields();
      let bankName = values.bankName.slice(0,1) === ' ' ? values.bankName.slice(1) : values.bankName;
      let purpose = values.purpose.slice(0,1) === ' ' ? values.purpose.slice(1) : values.purpose;
      let amount = values.amount;
      let months = values.months;
      let interest = values.interest;
      let penalty = values.penalty;

      if (bankName =='' || purpose == '' || amount == '' || months == '' || interest == '' || penalty == '') return;

      setConfirmLoading(true);
      let loanData = { bankName,purpose,amount,months,interest,penalty}
      if(!editingKey) DataService.create(loanData);
      else{
        DataService.update(editingKey,loanData)
        setEditingKey('');
        message.success(`Loan info updated`);
      }
      handleCancel();
    } catch (errInfo) {}
    
  };

  const handleCancel = () => {
    setVisible(false);
    setConfirmLoading(false);
    form.setFieldsValue({bankName:null,purpose:null,amount:null,months:null,interest:null,penalty:null});
  };
  
  const remove = key => {
    DataService.remove(key)
    message.success(`Loan info deleted`);
  };

  const columns = [
    {
      title: 'Bank',
      dataIndex: 'bankName',
      render: (_, record) => {
        return record.data().bankName
      }
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().amount} 
						thousandSeparator={true} 
					/>
				</div>
			),
    },
    {
      title: 'Purpose',
      dataIndex: 'purpose',
      render: (_, record) => {
        return record.data().purpose
      }
    },
    {
      title: 'Months',
      dataIndex: 'months',
      render: (_, record) => {
        return record.data().months
      }
    },
    {
      title: 'Interest',
      dataIndex: 'interest',
      render: (_, record) => {
        return record.data().interest+'%'
      }
    },
    {
      title: 'Penalty',
      dataIndex: 'penalty',
      render: (_, record) => {
        return record.data().penalty+'%'
      }
    },
    {
      title: 'Paid Amount',
      dataIndex: 'paid',
      render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={allLoansLoading ? '' : allLoans[record.id].paidAmount } 
						thousandSeparator={true} 
					/>
				</div>
			),
    },
    {
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
  ];
  return (
    <Form form={form} component={false} layout="vertical">
      <Flex alignItems="center" justifyContent="between" mobileFlex={false}>
        <Flex className="mb-1" mobileFlex={false}>
          <div className="mr-md-3">
            <Title level={4}>Loans List</Title>
          </div>
        </Flex>
        <Flex mobileFlex={false}>
          <div>
            <Button size={'small'} 
                    style={{
                      marginTop: 0,
                      paddingTop: 0,
                    }}
                    onClick={showModal}
                    type="primary" icon={<PlusCircleOutlined />} block>Add New</Button>
            <Modal
              title={editingKey ? "Edit Loan details" : "New Loan details"}
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="bankName" label="Bank Name" rules={[
                    {
                      required: true,
                      message: `bank name is required.`,
                    },
                  ]}>
                    <Input/>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="purpose" label="Loan Purpose" rules={[
                    {
                      required: true,
                      message: `purpose is required.`,
                    },
                  ]}>
                    <Input
                      className="w-100"                      
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12}>
                  <Form.Item name="amount" label="Loan Amount" rules={[
                    {
                      required: true,
                      message: `loan amount is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12}>
                  <Form.Item name="months" label="Months" rules={[
                    {
                      required: true,
                      message: `months is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12}>
                  <Form.Item name="interest" label="Interest Rate (%)" rules={[
                    {
                      required: true,
                      message: `interest rate is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12}>
                  <Form.Item name="penalty" label="Penalty Rate (%)" rules={[
                    {
                      required: true,
                      message: `penalty rate is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Modal>         
          </div>
        </Flex>
      </Flex>
      <Table
        dataSource={loansLoading ? [] : loans.docs} 
        columns={columns}
        loading={loansLoading}
        rowClassName="editable-row"
        size="middle"
      />
    </Form>
  );
};

export class Stores extends Component {
  render() {
    return (
      <EditableTable systemData={this.props.systemData}/>
    )
  }
}

export default Stores
